.svg-shapes__root {
}

.svg-shapes__item__odd {
  transition: all 0.2s ease-in-out;
  animation: anim1 5s ease-in-out infinite alternate;
}

.svg-shapes__item__even {
  transition: all 0.2s ease-in-out;
  animation: anim2 2s ease-in-out infinite alternate;
}

/* .svg-shapes__item:hover {
  transform: scale(1.2);
} */

@keyframes anim1 {
  0% {
    transform: scale(1);
    transform: rotate(90deg);
  }
  100% {
    transform: scale(1.4);
  }
}

@keyframes anim2 {
  0% {
    transform: scale(1.4);
    transform: rotate(-90deg);
  }
  100% {
    transform: scale(1);
  }
}

/* blue */
.svg-shapes__item__1 {
  position: absolute;
  top: 40%;
  left: 5%;
  width: 10%;
  z-index: 1;
}

/* orange */
.svg-shapes__item__2 {
  position: absolute;
  top: 50%;
  left: 30%;
  width: 7%;
  z-index: 1;
}

/* green */
